.CoaContainer{
    width: 100%;
    padding-top:4vmax ;
}
.coaForm{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}
.coaFormBox > p{
    /* color: rgb(255, 221, 0); */
}
.coaFormBox{
    width: 80%;
    padding: 4vmax 4vmax;
    border-radius: 2rem;
    border: 1px solid gray;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}
.inpBox{
    width: 100%;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    /* justify-content: ; */
    gap: 1rem;
}
.submitBox{
    display: flex;
    align-items: center;
    justify-content: center;
}
.cinputBox{
    width: 32%;
    border: none;
    /* border-bottom: 1px solid gray; */
}
.cinputBox > p{
    color: rgb(182, 182, 182);
}
.cinput{
    width: 100%;
    padding: 1rem;
    border: none;
    margin-top: 0.4rem;

    border-bottom: 1px solid gray;
    /* border-radius: 2rem; */
    /* outline: 2px solid yellowgreen; */
    cursor: pointer;

}
.sinput{
    width: 20%;
    padding: 1rem;
    border: 1px solid gray;
    border-radius: 2rem;
    background-color: rgb(255, 187, 0);
    color: white;
    font-size: large;
}
.sinput:hover{
    transform: scale(0.9);
    transition: all 0.5s;
    cursor: pointer;
    background-color: rgb(255, 157, 0);
}