.listCoaContainer{
    padding: 2vmax 10vmax;
}
.coaList{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.paginationBox{
    padding: 1vmax;
    /* border-top: 1px solid rgb(212, 212, 212); */
    display: flex;
    justify-content: flex-end;
}
.pagination{
    display: flex;
    gap: 0.5rem;
    text-decoration: none;
    gap: 1rem;
}
.pagination > li{
    text-decoration: none;
    padding: 0.5rem 1rem;
    color: white;

}
.pagination > li:hover{
    background-color: var(--bg3) !important;
}
.pagination > li > a{
    color: rgb(58, 58, 58);
    text-decoration: none;
}
.active{
    background-color: var(--bg1);
    color: white !important;
}