    color: var(--textC1);
    line-height: 22px;
}

.aboutBox>div:first-child>h2 {
    font: 600 2vmax 'Roboto';

}

.aboutBox>div:last-child {
    padding: 0vmax;
    text-align: center;
    padding-top: 0;
    /* background-color: aquamarine; */
    /* width: 100%; */
}

.aboutBox>div:last-child>img {
    height: 80%;
    /* box-shadow: 20px 20px 5px var(--textC2); */
}

/* aboutBox end */

/* contactBox */
.contactBox {
    width: 100%;
    display: flex;
    gap: 2rem;
    justify-content: center;
    align-items: center;
    padding: 3vmax;
}

.cItem {
    display: flex;
    gap: 1.5rem;
    justify-content: center;
    align-items: center;
    /* border: 1px solid var(--bg3); */
    border-radius: .4rem;
    /* padding: 1.8vmax 2.5vmax; */
    box-shadow: 0 0px 25px var(--textC3);
    overflow: hidden;
}

.cItem>div:first-child {
    padding: 1.5vmax 2.1vmax;
    background-color: var(--bg1);
}

.cItem>div>svg {
    font-size: 2.5vmax;
    color: var(--bg2);
    /* background-color: aqua; */

}

.cItem>div:last-child {
    /* padding: 1.8vmax 2.5vmax; */
    padding-right: 2vmax;

}

.cItem>div:last-child>h1 {
    font: 600 1.65vmax 'Roboto';
    color: var(--textC1);
}

.cItem>div:last-child>p {
    font: 500 1vmax 'Roboto';
    color: var(--textC1);
}

