.formContainer{
    width: 100%;
    padding: 0 7vmax;
    padding-top: 4vmax;
    display: flex;
    align-items: center;
    justify-content: center;
    /* border: 1px solid gainsboro; */
}
.form{
    width: 70vw;
    height: 70vh;
}

.formBox{
    width: 70vw !important;
    height: 100%;
    border: 1px solid gray;
    border-radius: 2rem;
    padding: 5vmax;
    display: flex;
    align-items: center;
    justify-content: center;

}
.formBox > h4{
    width: 100%;
    text-align: left;
    padding-left: 0.5vmax;
}
.input{
    width: 100%;
    padding: 1vmax;
    font: 600 1.2vmax 'Roboto';
    border-radius: 0.4rem;
}
